@import './Variables.scss';


.creator{
    @include maxWidthmaxHeight(100%,100vh);
    border: 1px solid black;
    .creator-title{
        width: 100%;
        padding:2rem;
        background: rgb(61, 6, 190);
        border-top: 5px solid black;
        p{
            text-transform: uppercase;
            font-size: 2rem;
            color:rgb(255, 255, 255);
        }
    }
    .creator-content{
        @include maxWidthmaxHeight(100%,100%);
        @include displayFlex(row);
        .creator-pattern{
            width:30%;
            height: 80%;
            background: rgb(212, 211, 211);
            .creator-pattern-title{
                @include maxWidthmaxHeight(100%,10%);
                p{
                    text-align: center;
                    color:rgb(45, 15, 153);
                    @include top50;
                }
            }
            .creator-pattern-content{
                @include maxWidthmaxHeight(100%,90%);
                .color-pattern{
                    @include maxWidthmaxHeight(100%,auto);
                    padding: 5px;
                    @include displayFlex(row);
                    justify-content: center;
                    .color-pattern-element{
                        width:50px;
                        height: 50px;
                        cursor: pointer;
                        clip-path: circle(50%);
                        transition: all 0.3s;
                        &:hover{
                            transform: scale(1.1);
                        }
                    }
                }
            }
        }
        .creator-draw{
            width:70%;
            height: 80%;
            background: rgb(255, 255, 255);
            .tshirt{
                margin: 0 auto;
                @include top50;
                width: 500px;
                height:600px;
                border: 1px solid black;
            }
        }
    }
}